import React, {useState} from 'react';
import './searchBox.styles.css';




 export const SearchBox =({onChangeHandeler, placeholder})=>{
     
    const [inputValue, setValue] = useState();
   function  inputChangeHandeler(e){
       const {value}= e.target;
       console.log('inputChangeHandeler ^^^^^^^^^', inputValue)
       setValue(value)
       console.log('after inputChangeHandeler ^^^^^^^^^', inputValue)
        onChangeHandeler(e)
    }
    return <input className="search" value={inputValue} type="search" placeholder={placeholder} onChange={inputChangeHandeler}/>
}





